md2-pagination {
  display: block;
  color: #0e59a5;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

md2-pagination:before,
md2-pagination:after {
  display: table;
  content: ''; }

md2-pagination:after {
  clear: both; }

md2-pagination .md2-pagination {
  display: inline-block;
  margin: 0.5rem 0;
  padding: 0; }

md2-pagination .md2-pagination li {
  position: relative;
  display: inline-block;
  width: 36px;
  vertical-align: top;
  text-align: center;
  line-height: 36px;
  border-radius: 100px;
  cursor: pointer;
  box-sizing: border-box; }

md2-pagination .md2-pagination li:hover {
  background: rgba(0, 0, 0, 0.12); }

md2-pagination .md2-pagination li.disabled,
md2-pagination .md2-pagination li.disabled:hover {
  pointer-events: none;
  background: transparent;
  cursor: default;
  opacity: 0.5; }

md2-pagination .md2-pagination li.active,
md2-pagination .md2-pagination li.active:hover {
  background: #106CC8;
  color: #fff;
  cursor: default; }

md2-pagination .md2-pagination li svg {
  fill: currentColor;
  margin-bottom: -7px; }

md2-pagination .md2-rows-select {
  display: inline-block;
  margin: 0.5rem 0;
  padding: 0;
  float: right;
  color: rgba(0, 0, 0, 0.54);
  line-height: 36px; }

md2-pagination .md2-rows-select select {
  border: 0;
  outline: 0; }
